.about {
    padding: 30px 40px 30px 30px;
    position: relative;
    font-family: 'La Belle Aurore', cursive;
}

.about h1 {
    color: white;
}
.about .about-content {
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-line-pack: space-evenly;
        align-content: space-evenly;
}

.about-content .item {
    margin-right: 10px;
}

a {
    color: #1890ff;
    background-color: transparent;
    text-decoration: none;
    outline: none;
    cursor: pointer;
    -webkit-transition: color .3s;
    -o-transition: color .3s;
    transition: color .3s;
}



